$main-colour: #ddd;
$colour-blue: #00EEEE;
$colour-pink: #EE00EE;

#anothertwosections {
    transform: translateY(-100px);
    background: linear-gradient(45deg, #EE00EE, #00EEEE, #EEEE00);
}

#technologies {
    width: 100%;
    height: auto;
    overflow: hidden;
    line-height: 110px;


    %h3-normal {
        width: 500%;
        position: relative;
        font-size: 111px;
        text-transform: uppercase;
        color: rgba(0, 0, 0, 0.9);
        z-index: 10;
        animation: technologiesfirsttitle 12s linear infinite;
        cursor: default;
    }

    %boxes-normal {
        background: rgba(221, 221, 221, 0.47);
        height: auto;
        width: 68%;
        text-align: center;
        padding: 25px 15px 25px 15px;
        margin: 15px;
        border-radius: 5px;
        transition: all 400ms;
        z-index: 10;
        backdrop-filter: blur(12px);
    }

    #masteredtechnologies {
        width: 100%;
        height: 640px;
        padding-top: 50px;
        margin-top: 0px;
        text-align: start;

        #inmasteredtechnologiestitle {
            width: 100%;
            overflow: hidden;

            h3 {
                @extend %h3-normal;
            }

            @keyframes technologiesfirsttitle {
                from {
                    transform: translateX(-1428px);
                }

                to {
                    transform: translateX(0px);
                }
            }
        }

        #inmasteredtechnologies {
            position: absolute;
            margin-left: 40px;
            display: grid;
            width: 60%;
            padding-top: 66px;
            grid-template-columns: repeat(5, 1fr);

            div {
                @extend %boxes-normal;

                img {
                    width: 58%;
                }
            }

            div:hover {
                box-shadow: 2px 2px 10px $main-colour;
                transform: translateY(-20px) rotateY(-15deg) rotateX(15deg);
            }
        }

        svg {
            width: 50%;
            position: absolute;
            right: 0%;
            top: 80px;
        }

        #wave2 {
            animation: wave2 10s infinite linear;

            @keyframes wave2 {

                0%,
                100% {
                    d: path('M763.725 966.242C481.725 436.742 217.325 820.241 233.725 472.241C326.414 211.758 834.225 630.242 744.225 174.242C654.225 -281.758 1514.72 435.241 1172.22 472.241C829.725 509.242 1045.72 1495.74 763.725 966.242Z');
                }

                33.33333333% {
                    d: path('M763.725 966.242C284.531 869.508 -74.9999 484 233.725 472.241C542.45 460.483 280.481 153.246 744.225 174.242C1207.97 195.238 1470.73 411.603 1172.22 472.241C873.724 532.88 1242.92 1062.98 763.725 966.242Z');
                }

                66.66666666% {
                    d: path('M763.726 966.242C328.834 728.816 435.468 1396.78 233.725 472.241C59.916 -234.982 520.683 382.101 744.226 174.242C967.768 -33.6176 833.95 733.482 1172.23 472.241C1510.5 211 1198.62 1203.67 763.726 966.242Z');
                }
            }
        }
    }

    #technologiesindevelopment {
        width: 100%;
        height: auto;
        padding: 10px 40px 30px 0px;
        text-align: end;
        padding-bottom: 0px;

        #inmasteredtechnologiestitle2 {
            width: 100%;
            overflow: hidden;


            h3 {
                @extend %h3-normal;
                animation: technologiesfirsttitle2 12s linear infinite;
            }

            @keyframes technologiesfirsttitle2 {
                from {
                    transform: translateX(-2800px);
                }

                to {
                    transform: translateX(-4580px);
                }
            }
        }

        #imagesandboxes {
            display: flex;

            #intechnologiesindevelopment {
                flex-basis: 55%;
                display: grid;
                margin-top: 70px;
                grid-template-columns: repeat(3, 1fr);
                transform: translateX(50px);

                div {
                    @extend %boxes-normal;
                    width: 56%;
                    padding: 25px 15px 15px 15px;
                    margin: 15px;

                    img {
                        width: 58%;
                    }
                }

                #threejs {
                    grid-column: 2 / 3;
                }

                div:hover {
                    box-shadow: 2px 2px 10px $main-colour;
                    transform: translateY(-20px) rotateY(-15deg) rotateX(15deg);
                }
            }

            div {
                justify-self: center;
                align-self: flex-end;
                flex-basis: 20%;

                #svgtest {
                    width: 150%;
                }
            }
        }
    }
}



