$main-colour: #ddd;
$colour-blue: #00EEEE;
$colour-pink: #EE00EE;

#letstalkclick {
    width: 100%;
    height: 90vh;
    background: linear-gradient(135deg, #00EEEE, #EE00EE);
    text-align: center;
    padding-top: 70px;
    padding-bottom: 270px;

    #socialmedia {
        height: auto;
        width: 100%;

        #socialmediatitle {
            font-size: 70px;
            padding-top: 25px;
            color: rgba(238, 238, 238, 0.7);
            transition: all 300ms;
            width: 40%;
        }

        #socialmediatitle:hover {
            color: rgba(238, 238, 238, 0.9);
            cursor: default;
        }

        #insocialmedia {
            width: 613px !important;
            height: 45px;
            padding-top: 20px;
            padding-bottom: 20px;
            background: rgba(248, 247, 247, 0.295);
            border-radius: 10px;
            margin-bottom: 53px;
            margin-top: 30px;
            box-shadow: 0px 5px 10px rgba(237, 237, 237, 0.392);


            .linksocialmedia {
                width: 5px !important;
                height: auto;
                margin-left: 60px;
                margin-right: 60px;
                transition: all 300ms;

                .imgup {
                    transform: translateX(-170px);
                }

                img {
                    position: absolute;
                    transform: translateX(-20px);
                }

                img:hover {
                    transform: translateY(-5px);
                }

                img:nth-child(2) {
                    transform: translateX(-10px);
                }
            }
        }
    }

    #forms {
        width: 460px;
        height: 381px;
        margin: 0px auto;
        text-align: justify;
        margin-top: 45px;
        background: rgba(0, 0, 0, 0.296);
        padding: 10px;
        border-radius: 10px;
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.651);

        label {
            width: 60%;
            color: rgba(243, 243, 243, 0.667);
            text-transform: uppercase;
            font-size: 20px;
            display: block;
            text-align: center;
            margin-bottom: 6px;
        }

        label:nth-child(1) {
            margin-top: 15px;
        }

        input[type="text"],
        input[type="number"],
        input[type="email"] {
            text-align: center;
            margin-left: 18.5%;
            width: 60%;
            height: 23px;
            border-radius: 5px;
            outline: transparent;
            border: transparent;
            padding: 5px;
            opacity: 0.8;
            margin-bottom: 17px;
        }

        input[type="text"]:focus,
        input[type="number"]:focus,
        input[type="email"]:focus {
            opacity: 1;
            filter: brightness(110%) saturate(110%);
        }

        input[type="submit"] {
            width: 18%;
            height: 30px;
            border-radius: 3px;
            opacity: 0.8;
            outline: none;
            border: transparent;
            margin-left: 39.5%;
            cursor: pointer;
            margin-bottom: 20px;
            transition: all 400ms;
            color: rgba(237, 237, 237, 0.566);
            background: black;
        }

        input[type="submit"]:hover {
            opacity: 1;
            color: rgba(237, 237, 237, 0.7);
        }
    }
}

#letstalkfooter {
    width: 100%;
    height: 70px;
    color: rgba(238, 238, 238, 0.7);
    margin-top: 0px;
    background: conic-gradient(from 180deg at 50% 50%, #6764FF -91.46deg, #C064F9 93.04deg, #9464FC 181.12deg, #6764FF 268.54deg, #C064F9 453.04deg);
    text-align: center;
    line-height: 70px;
    font-size: 25px;
}

@media (max-width: 1289px) {
    #letstalkclick {
        #socialmedia {
            #socialmediatitle {
                font-size: 55px;
            }
        }
    }
}

@media (max-width: 1010px) {
    #letstalkclick {
        #socialmedia {
            #socialmediatitle {
                font-size: 40px;
            }
        }
    }
}

@media (max-width: 737px) {
    #letstalkclick {
        #socialmedia {
            #socialmediatitle {
                width: 100%;
                font-size: 45px;
            }

            #insocialmedia {
                width: 80% !important;

                .linksocialmedia {
                    margin-left: 10% !important;
                    margin-right: 10% !important;
                }
            }
        }

        #forms {
            width: 62.8%;
        }
    }
}

@media (max-width: 652px) {
    #letstalkclick {
        #forms {
            input[type="submit"] {
                width: 30%;
                margin-left: 35%;
            }
        }
    }
}


@media (max-width: 531px) {
    #letstalkclick {
        #socialmedia {
            #socialmediatitle {
                width: 100%;
                font-size: 35px;
            }
        }

        #forms {
            width: 75%;

            label {
                width: 70% !important;
            }

            input[type="text"],
            input[type="number"],
            input[type="email"] {
                margin-left: 13.5%;
                width: 70%;
            }

            input[type="submit"] {
                width: 30%;
                margin-left: 35%;
            }
        }
    }
}


@media (max-width: 430px) {
    #letstalkclick {
        #socialmedia {
            #insocialmedia {

                .linksocialmedia {
                    margin-left: 12% !important;
                    margin-right: 12% !important;
                }
            }
        }
    }
}


@media (max-width: 310px) {
    #letstalkclick {
        #socialmedia {
            #socialmediatitle {
                width: 100%;
                font-size: 35px;
            }

            #insocialmedia {
                height: 120px;
                width: 80% !important;
                padding-bottom: 10px;

                display: grid;
                grid-template-columns: repeat(2, 1fr);

                .linksocialmedia {
                    margin-left: 30% !important; 

                    .imgup {
                        transform: translateX(0px) !important;
                    }

                    img {
                        position: relative;
                        transform: translateX(0px) !important;
                    }

                    img:hover {
                        transform: translateY(0px) !important;
                    }

                    img:nth-child(2) {
                        transform: translateX(0px) !important;
                    }
                }
            }
        }

        #forms {
            height: 355px;
            width: 65%;

            label {
                font-size: 15px;
                width: 75% !important;
            }

            input[type="text"],
            input[type="number"],
            input[type="email"] {
                margin-left: 10%;
                width: 75%;
            }

            input[type="submit"] {
                width: 45%;
                margin-left: 28%;
            }
        }
    }
}

@media (max-width: 260px) {
    #letstalkclick {
        #socialmedia {
            #socialmediatitle {
                width: 100%;
                font-size: 35px;
            }

            #insocialmedia {
                .linksocialmedia {
                    margin-left: 28% !important; 
                }
            }

        }

        #forms {
            width: 70%;

            input[type="submit"] {
                width: 50%;
                margin-left: 25%;
            }
        }
    }

    #letstalkfooter {
        font-size: 17px;
    }
}


@media (max-width: 235px) {
    #letstalkclick {
        #socialmedia {
            #insocialmedia {
                .linksocialmedia {
                    margin-left: 25% !important; 
                }
            }
        }
    }
}

@media (max-width: 210px) {
    #letstalkclick {
        #socialmedia {
            #insocialmedia {
                .linksocialmedia {
                    margin-left: 23% !important; 
                }
            }
        }
    }
}