$main-colour: #ddd;
$colour-blue: #00EEEE;
$colour-pink: #EE00EE;

#footer {
    height: auto;
    background: linear-gradient(180deg, #5DF1EA, $colour-blue);
    opacity: 1;
    text-align: center;
    line-height: 70px;
    cursor: default;
    z-index: 50;
    position: relative;

    p {
        font-size: 19px;
        color: rgba(0, 0, 0, 0.9);
        opacity: 1;
        font-weight: bolder;
       

        span:hover{
            transition: all 200ms;
            color: rgba(255, 255, 255, 0.675);
        }
    }
}

@media(max-width:627px) {
    #footer {
        p {
            font-size: 17px;
        }
    }
}

@media(max-width:476px) {
    #footer {
        p {
            font-size: 15px;
        }
    }
}

@media(max-width:437px) {
    #footer {
        p {
            font-size: 13px;
        }
    }
}

@media(max-width:362px) {
    #footer {
        p {
            font-size: 11px;
        }
    }
}

@media(max-width:300px) {
    #footer {
        p {
            font-size: 9px;
        }
    }
}

@media(max-width:300px) {
    #footer {
        p {
            font-size: 7px;
        }
    }
}