$main-colour: #ddd;
$colour-blue: #00EEEE;
$colour-pink: #EE00EE;

#header {
    height: 60px;
    width: 100%;
    display: flex;
    position: absolute;
    backdrop-filter: blur(10px);
    background: rgba(221, 221, 221, 0.05);
    text-align: center;
    z-index: 2000;
    // filter: blur(12px);

    div {
        flex-basis: 28%;
        line-height: 54px;
        overflow: hidden;

        h1 {
            animation: fallheader 600ms linear;
        }

        h1 a {
            color: rgba(238, 238, 238, 0.68);
            font-size: 22px;
            font-weight: normal;
            text-decoration: none;
            transition: all 400ms;

        }

        h1 a:hover {
            color: rgba(238, 238, 238, 1);
            filter: brightness(110%) saturate(110%);
        }
    }

    nav {
        flex-basis: 38%;
        line-height: 60px;
        overflow: hidden;

        #menu {
            display: flex;
            padding-right: 70px;
            list-style: none;
            justify-content: center;
            align-items: center;

            li a {
                text-decoration: none;
                margin: 0px 10px 0px 10px;
                color: rgba(238, 238, 238, 0.68);
                font-size: 18px;
                transition: all 400ms;
            }

            li a:hover {
                color: rgba(238, 238, 238, 1);
                filter: brightness(110%) saturate(110%);
            }
        }

        #hamburgerlogo{
            color: rgba(238, 238, 238, 0.6);
            font-size: 23px;
            text-decoration: none;
            display: none;
        }
    }

    .hamburger-menu {
        position: absolute;
        top: 0;
        left: 0;
        display: none;
        padding: 10px 0px;
        width: 100%;
        height: 300px;
        background: linear-gradient(90deg, rgb(91, 228, 255), rgb(227, 100, 255));
        backdrop-filter: blur(10px);
        animation: hamburguermenu 300ms linear;

        ul {
            display: flex;
            height: 100%;
            flex-direction: column;
            justify-content: space-around;
            align-items: flex-start;
            list-style: none;

            li {
                a {
                    color: rgba(238, 238, 238, 0.7);
                    text-decoration: none;
                    font-size: 21px;
                }
            }
        }

        .close {
            display: block;
            position: absolute;
            right: 35px;
            top: 10px;
            color: rgba(238, 238, 238, 0.7);
            font-size: 35px;
            text-decoration: none;
        }
    }

    @keyframes hamburguermenu {
        from{
            transform: translateY(-350px);
        }

        to{
            transform: translateY(0px);
        }
    }

    .active{
        display: block;
    }
}

@media (max-width: 1297px) {
    #header {
        div {
            h1 a {
                font-size: 17px;
            }
        }

        nav {
            #menu {
                padding-right: 55px;

                li a {
                    font-size: 15px;
                }
            }
        }

        .hamburger-menu {
            padding: 10px 0px;
            margin: 0px !important;
        }
    }
}

@media (max-width: 1111px) {
    #header {
        div {
            margin-left: 5% !important;

            h1 a {
                font-size: 15px;
            }
        }

        nav {
            #menu {
                padding-right: 5px;

                li a {
                    font-size: 13px;
                }
            }
        }
    }
}

@media (max-width: 875px) {
    #header {
        nav {
            flex-basis: 48%;
        }
    }
}

@media (max-width: 800px) {
    #header {
        div {
            text-align: start;
            flex-basis: 70%;
            margin-left: 40px !important;

            h1 a {
                font-size: 20px;
            }
        }

        nav {
            flex-basis: auto;
            height: auto;

            ul{
                li{
                    a{
                        display: none;
                    }        
                }
            }
           
            #hamburgerlogo{
                height: 20px;
                margin-right: 0px;
                display: block;
                text-align: end;
            }
        }
    }
}


@media (max-width: 480px) {
    #header {
        div {
            margin-left: 35px !important;

            h1 a {
                font-size: 17px;
            }
        }

        nav {
            #hamburgerlogo{
                margin-right: 10px;
            }
        }
    }
}

@media (max-width: 358px) {
    #header {
        div {
            margin-left: 25px !important;

            h1 a {
                font-size: 15px;
            }
        }
    }
}

@media (max-width: 282px) {
    #header {
        div {
            h1 a {
                font-size: 12px;
            }
        }
    }
}

@media (max-width: 270px) {
    #header {
        .hamburger-menu {
            .close {
                right: 25px;
                top: 10px;
                font-size: 30px;
            }
        }
    }
}

@media (max-width: 240px) {
    #header {
        div {
            h1 a {
                font-size: 10px;
            }
        }
    }
}

@media (max-width: 210px) {
    #header {
        div {
            h1 a {
                font-size: 8px;
            }
        }
    }
}