@use './inproyects/proyect';

$main-colour: #ddd;
$colour-blue: #00EEEE;
$colour-pink: #EE00EE;

#proyects {
    width: 100%;
    height: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    overflow: hidden;
    padding-top: 9px;
    padding-bottom:35px;

    #inproyects1 {
        height: auto;
        width: 60%;
        padding: 15px;
        text-align: center;
        transform: translateX(-15%);

        .proyect {
            height: auto;
            width: auto;
            margin-top: 100px;
            transition: all 300ms;
            text-decoration: none;
            
            div{
                width: 125%;
                height: 161px;
                background-size: 100% 100%;
                margin-left: 10% !important;
                margin: 10px auto;
                border-radius: 2px;
                transition: all 300ms;
            }

            .divimageproyect1{
                background-image: url("../images/proyects/proyect1.png");
            }
            
            .divimageproyect2{
                background-image: url("../images/proyects/proyect2.png");
            }

            .divimageproyect3{
                background-image: url("../images/proyects/proyect3.png");
            }

            .divimageproyect4{
                background-image: url("../images/proyects/proyect4.png");
            }


            div:hover {
                transform: translateX(-30px);
                box-shadow: 30px 0px 10px rgba(225, 225, 225, 0.264);
            }
        }
    }

    #inproyects2 {
        height: auto;
        width: 60%;
        padding-top: 0px;
        text-align: start;
        transform: translateX(-20%);

        h3 {
            width: 135%;
            font-size: 820%;
            text-transform: uppercase;
            font-style: italic;
            margin: 22.5px 0px;
            opacity: 0.9;
            transition: all 500ms;
        }

        h3:last-child {
            margin-bottom: 0px;
        }

        h3:hover {
            letter-spacing: 5px;
            cursor: default;
            font-family: 'Dancing Script', cursive;
            transform: translateX(25px);
            background-image: linear-gradient(90deg, rgb(181, 0, 0), $colour-pink);
            background-size: 500% 500%;
            -webkit-text-fill-color: transparent;
            -webkit-background-clip: text;
            background-clip: text;
            animation: button 9s infinite ease;
        }
    }
}

