$main-colour: #ddd;
$colour-blue: #00EEEE;
$colour-pink: #EE00EE;

#home {
    height: auto;
    width: 100%;
    background: linear-gradient(45deg, rgb(238, 0, 238), rgb(253, 201, 80), rgb(0, 238, 238));
    padding: 70px 0px 70px 0px;
    display: flex;
    padding-bottom: 250px;

    #inhome {
        flex-basis: 60%;
        justify-self: center;
        margin: 0px;
        text-align: center;

        #bigh1 {
            margin: 10px;
            margin-top: 50px;
            font-size: 111px;
            text-transform: uppercase;
            font-style: italic;
            opacity: 0.9;
            background: #ccc;
            background-image: linear-gradient(90deg, $colour-blue, #EEEE00, $colour-pink);
            background-size: 600% 600%;
            -webkit-text-fill-color: transparent;
            -webkit-background-clip: text;
            background-clip: text;
            animation: first-h1 12s infinite ease;
        }

       @keyframes first-h1 {
           0% {
               background-position: 0% 50%;
           }

           50% {
               background-position: 100% 50%;
           }

           100% {
               background-position: 0% 50%;
           }
       }

        p {
            width: 55%;
            text-align: center;
            margin-top: 40px;
            font-size: 22px;
            line-height: 28px;
        }

        #cv {
            width: auto;
            height: auto;
            margin-top: 20px;

            #buttoninhome {
                width: 190px;
                height: 70px;
                padding: 15px;
                border-radius: 5px;
                text-align: end;
                font-size: 19px;
                border: transparent;
                background: rgba(221, 221, 221, 0.36);
                margin-top: 40px;
                cursor: pointer;
                z-index: 10;
                position: relative;
                transition: all 400ms;

                img {
                    position: absolute;
                    top: 27.5%;
                    left: 5.2%;
                    animation: heart0 1s linear infinite;
                }

                @keyframes heart0 {

                    0%,
                    100% {
                        transform: scale(1, 1);
                    }

                    50% {
                        transform: scale(1.2, 1.2);
                    }
                }
            }

            #buttoninhome:hover {
                transform: translateY(-10px);
                filter: brightness(110%) saturate(110%);
                box-shadow: 0px 5px 20px $colour-blue;
                background: rgba(221, 221, 221, 0.41);
            }
        }
    }

    #svgbox1 {
        flex-basis: 45%;
        align-self: center;
        justify-self: center;
        z-index: 1;
        animation: svgboxhome 600ms ease-in-out;
        margin-right: 70px;

        img{
            padding-top: 60px;
            width: 100%;
        }
    }

    @keyframes svgboxhome {
        from {
            transform: scale(0.5, 0.5);
            opacity: 0.3;
        }

        to {
            transform: scale(1, 1);
            opacity: 1;
        }
    }
}

//max-width

@media(max-width:1406px) {
    #home {
        #inhome {
            padding-left: 60px;

            #bigh1 {
                font-size: 100px;
            }

            p {
                text-align: center;
            }
        }
    }
}

