//modules
@use 'header';
@use 'home';
@use 'aboutme';
@use 'proyects';
@use 'technologies';
@use 'letstalk';
@use 'footer';


//variables
$main-colour: #ddd;
$colour-blue: #00EEEE;
$colour-pink: #EE00EE;

* {
    margin: 0px auto;
    padding: 0px;
    scroll-behavior: smooth;
    font-family: 'Ubuntu', sans-serif;
}

::-webkit-scrollbar {
    width: 0em;
    height: 0em;
    background: transparent;
}


//blur thing that separates every section located in body
#firststicker {
    width: 100%;
    height: 50px;
    backdrop-filter: blur(12px);
    background: rgba(221, 221, 221, 0.43);
    transform: translateY(-25px);
    z-index: 1000;
    position: relative;
}

#secondsticker {
    width: 100%;
    height: 50px;
    top: 317.7%;
    backdrop-filter: blur(12px);
    background: rgba(221, 221, 221, 0.43);
    z-index: 1000;
    position: relative;
    transform: translateY(-75px);
}

//route-error
#error {
    text-align: center;
    padding-top: 15%;

    h1 {
        font-size: 161px;
        opacity: 0.9;
    }

    p {
        font-size: 50px;
        opacity: 0.8;
    }
}

.observerbtn{
    position: fixed;
    top: 50%;
    left: 2%;
    z-index: 2000;
    text-align: center;
    padding: 10px;
    width: 119px;
    height: 50px;
    border: transparent;
    border-radius: 5px;
    cursor: pointer;
    background-image: none;
    backdrop-filter: blur(10px);
    box-shadow: 0px 5px 20px $colour-blue;

    .btnletstalk {
        padding: 0px;
        margin: 0px auto;
        width: 100%;
        height: 140%;
        display: block;
        color: $main-colour;
        text-decoration: none;
        font-size: 22px;
        background-image: none;
        transform: translateY(-10px);
        line-height: 70px;
    }
}

.overbtnletstalk{        
    text-align: center;
    z-index: 100;
    padding: 10px;
    width: 119px;
    height: 50px;
    box-shadow: 0px 5px 20px $colour-blue, 0px -5px 20px $colour-pink;
    border: transparent;
    border-radius: 5px;
    opacity: 0.8;
    cursor: pointer;
    background-image: linear-gradient(90deg, $colour-blue, #EEEE00, $colour-pink);
    background-size: 600% 600%;

    .btnletstalk {
        padding: 0px;
        margin: 0px auto;
        width: 100%;
        height: 140%;
        display: block;
        color: $main-colour;
        text-decoration: none;
        font-size: 22px;
        background-image: none;
        transform: translateY(-10px);
        line-height: 70px;
    }
}

