$main-colour: #ddd;
$colour-blue: #00EEEE;
$colour-pink: #EE00EE;

//two sections together, body too
#twosections {
    background: linear-gradient(135deg, #9fffff, #ff7cff, #ffff62);
    transform: translateY(-50px);
    z-index: 100;
    position: relative;
}

//about me section
#aboutme {
    width: 100%;
    height: 590px;
    text-align: justify;
    padding-top: 170px;
    display: flex;

    article {
        width: 45%;
        height: auto;
        margin: 0px;
        line-height: 32px;
        font-size: 19px;
        padding: 10px 15px 15px 45px;
        border-radius: 7px;
        font-weight: lighter;

        #inaboutme {
            width: 120%;
            text-align: center;
            font-size: 45px;
            margin: 0px 0px 58px 30px;
            transition: all 300ms;

            h3 {
                font-size: 111px;
                width: 75%;
                padding-top: 30px;
                height: 65px;
                opacity: 0.9;
                transition: all 300ms;
            }

            h3:hover {
                transform: translateY(-10px);
                letter-spacing: 5px;
                cursor: default;
                font-family: 'Dancing Script', cursive;
                background-image: linear-gradient(90deg, rgb(181, 0, 0), $colour-pink);
                background-size: 500% 500%;
                -webkit-text-fill-color: transparent;
                -webkit-background-clip: text;
                background-clip: text;
                animation: button 9s infinite ease;
            }
        }

        #inaboutme2 {
            width: 100%;
            padding: 30px;
            margin-left: 65px;
            background: rgba(245, 245, 220, 0.23);
            border-radius: 10px;
            transition: all 300ms;
        }
    }

    div{
        align-self: center;
        justify-self: center;
        
        #svgcouple {
            width: 150%;
            transition: all 300ms;
        }
    }
    
}

@media(max-width: 1373px) {
    #aboutme {
