$main-colour: #ddd;
$colour-blue: #00EEEE;
$colour-pink: #EE00EE;

.allproyects {
    width: 100%;
    height: auto;
    padding-top: 70px;
    background: linear-gradient(315deg, rgba(0, 250, 250, 0.478), rgba(250, 0, 255, 0.478));
    margin: 0px auto;
    text-align: center;
    padding-top: 15vh;
    padding-bottom: 15vh;

    .allproyectstitle {
        font-size: 111px;
        opacity: 0.9;
        text-transform: uppercase;
        letter-spacing: 1px;
        transition: all 300ms;
        cursor: default;
    }

    .allproyectstitle:hover {
        opacity: 1;
        filter: brightness(110%) saturate(110%);
    }

    .website-reppository {
        width: 50%;
        height: auto;
        opacity: 0.9;
        transition: all 300ms;
        margin-bottom: 50px;
        margin-top: 50px;
        display: flex;

        .in-web-rep {
            width: 25%;
            height: 60px;
            line-height: 60px;
            border-radius: 5px;
            background: rgba(245, 245, 245, 0.348);

            a {
                display: block;
                font-weight: bold;
                font-size: 25px;
                color: rgba(0, 0, 0, 0.8);
                text-decoration: none;
                transition: all 300ms;
            }

            a:hover {
                color: rgba(0, 0, 0, 1);
            }
        }

    }

    .allproyectsaboutproyect {
        width: 70%;
        height: auto;
        background: rgba(0, 0, 0, 0.296);
        border-radius: 5px;
        box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.65);
        padding: 10px;
        overflow: hidden;
        margin-bottom: 70px;
        margin-top: 70px;

        .inallproyectsaboutproyect {
            width: 50%;
            border-radius: 5px;
            margin: 10px auto;
            padding: 10px;

            h4 {
                padding-bottom: 10px;
                font-size: 27px;
                text-transform: uppercase;
                opacity: 0.9;
                color: rgba(238, 238, 238, 0.786);
                transition: all 300ms;
            }

            h4:hover {
                color: rgba(238, 238, 238, 0.986);
            }

            p {
                padding-bottom: 10px;
                font-size: 20px;
                opacity: 0.9;
            }

            div {
                height: auto;
                width: auto;

                img {
                    margin-right: 10px;
                    margin-left: 10px;
                    transition: all 300ms;
                    margin-top: 5px;
                    margin-bottom: 5px;
                    animation: inimages 600ms ease-in;
                }

                img:hover {
                    transform: translateY(-5px);
                }

                @keyframes inimages {
                    from {
                        transform: translateY(100px);
                    }

                    to {
                        transform: translateY(0px);
                    }
                }
            }
        }
    }

    #video-section {
        margin-top: 40px;

        h4 {
            font-size: 25px;
            width: 70%;
            margin-bottom: 20px;
        }

        iframe {
            width: 60%;
            height: 450px;
        }
    }
}


@media (max-width: 1195px) {
    .allproyects {
        #video-section {

            h4 {
                font-size: 23px;
                width: 85%;
            }
    
            iframe {
                width: 72%;
            }
        }
    }
}

@media (max-width: 980px) {
    .allproyects {
        .allproyectstitle {
            font-size: 90px;
        }

        .website-reppository {
            width: 90%;

            .in-web-rep {
                width: 25%;
            }

        }

        .allproyectsaboutproyect {
            width: 70%;

            .inallproyectsaboutproyect {
                width: 85%;

                p {
                    font-size: 17px;
                }
            }
        }

        #video-section {
            h4 {
                font-size: 21px;
            }
    
            iframe {
                height: 380px;
            }
        }
    }
}

@media (max-width: 740px) {
    .allproyects {
        .allproyectstitle {
            font-size: 70px;
        }
        
        #video-section {
            h4 {
                width: 90%;
            }
    
            iframe {
                height: 300px;
            }
        }
    }
}

@media (max-width: 565px) {
    .allproyects {
        .allproyectstitle {
            font-size: 50px;
        }

        .website-reppository {
            .in-web-rep {
                width: 40%;
            }
        }
        
        .allproyectsaboutproyect {
            margin-bottom: 40px !important;
        }

        #video-section {
            margin-top: 0px !important;
            h4 {
                font-size: 17px;
            }
        }
    }
}

@media (max-width: 420px) {
    .allproyects {
        .allproyectstitle {
            font-size: 30px;
        }

        .website-reppository {
            .in-web-rep {
                width: 45%;
            
                a {
                    font-size: 20px;
                }
            }
        }

        .allproyectsaboutproyect {
            width: 75%;

            .inallproyectsaboutproyect {
                h4 {
                    font-size: 24px;
                }

                p {
                    font-size: 15px;
                }
            }
        }

        #video-section {
            h4 {
                font-size: 15px;
            }
    
            iframe {
                height: 200px;
            }
        }
    }
}

@media (max-width: 290px) {
    .allproyects {
        .allproyectsaboutproyect {
            .inallproyectsaboutproyect {
                h4 {
                    font-size: 20px;
                }

                p {
                    font-size: 14px;
                }

            }
        }

        #video-section {
            h4 {
                font-size: 12px;
            }
        }
    }
}

@media (max-width: 260px) {
    .allproyects {
        .allproyectstitle {
            font-size: 20px;
        }

        .website-reppository {
            .in-web-rep {
                a {
                    font-size: 15px;
                }
            }
        }
    }
}